
import Toast from 'vux/src/components/toast/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

export default {
name: '',
components: {
Toast,
Flexbox,
FlexboxItem,
Checker,
CheckerItem,
XInput,
Group,
XTextarea,
Icon
},
data () {
return {
type: '',
ids: [],
toastData: {
isShow: false,
type: 'success',
text: '购买成功'
},
defaultAddress: {},
lists: [],
amounts: 0,
freight: 0,
nofreight: 0,
isBill: 0,
billType: '',
json: {
addressid: '',
buyer: '',
invoice: 0,
invoicebank: '个人发票',
invoicecode: '',
invoicetitle: '',
invoicecontext: '化妆品',
source: 2
}
}
},
destroyed () { // 销毁时显示
this.componentsShow(true)
},
mounted () { // 隐藏
this.componentsShow(false)
},
created () {

let type = this.$route.params.type / 1
console.log(type, 'type')
let lists = JSON.parse(sessionStorage.getItem('settleLists'))
let address0 = JSON.parse(localStorage.getItem('defaultAddress'))
this.type = type
if (address0) {
this.defaultAddress = address0
} else {
this.getAddressList()
}
this.lists = lists
if (type === 1 || type === 2) { // 详情页结算
this.ids = [lists[0].proscar]
} else if (type === 3) { // 购物车结算
lists.map((item, index) => {
this.ids.push(item.id) 
})
}
this.amounts = lists.reduce((sum, item, index, array) => {
sum += item.proamount * item.number
return sum
}, 0)
if (type !== 2) { // 非积分
console.log(this.ids)
let nofreight = sessionStorage.getItem('nofreight') / 1
let freight = sessionStorage.getItem('freight') / 1
this.nofreight = nofreight
if (this.amounts >= nofreight) {
this.freight = 0
} else {
this.amounts += freight
this.freight = freight
}
} else {
console.log(lists)

}
},
methods: {
componentsShow (bool) {
this.$store.state.bottomShow = bool
},
change (val) {
console.log(val)
},
getAddressList () { // 获取所有地址
this.axios.post('member/addresslist', {
order: 'ASC',
pageNum: 1,
pageSize: 100
}).then((response) => {
this.defaultAddress = response.data[0]
localStorage.setItem('defaultAddress', JSON.stringify(response.data[0]))
})
},
settle () { // 下单
let orderType = 1
let url = ''
this.json.addressid = this.defaultAddress.id

if (this.type === 2) {
orderType = 2
url = 'account/accountintegral'
this.json.pros = this.ids.join()
this.json.nubmer = this.lists[0].num
} else if (this.type === 4) {
url = 'account/accountsmall'
} else {
orderType = 1
this.json.proscar = this.ids.join()
url = 'account/account'
}
this.axios.post(url, this.json).then((response) => {
if (response.success) {
sessionStorage.removeItem('settleLists')
this.toastData = {
isShow: true,
type: 'success',
text: '购买成功'
}
let timer = setTimeout(() => {
this.$router.push({name: 'order', params: {type: orderType, id: 0}})
clearTimeout(timer)
}, 1000)
} else {
this.toastData = {
isShow: true,
type: 'warn',
text: response.message
}
}
})
}
}
}



